import _ from 'lodash'
import axios from 'axios'
import moment from 'moment'
import { gps } from '../../../../util/random'
import ComprehensiveMonitorDetail from './ComprehensiveMonitorDetail.vue'

axios.defaults.timeout = 3000

export default {
  components: { ComprehensiveMonitorDetail },
  data() {
    return {
      alarms: [
        // TODO  step-1 
        // {
        //   label: '', // 
        //   count: 4, // 
        //   ratio: 1, // >0:<0:
        //   color: '#ff0000', // 
        //   disable: false, // 
        //   list: [
        //     // 
        //     {
        //       lng: 114.091058, // 
        //       lat: 32.148624, // 
        //       name: '', // 
        //       level: '', // 
        //       time: '2020-09-07 15:53:12', // 
        //       position: '456', // 
        //       cover: 'https://img1.wxzxzj.com/vpc-example-cover-your-name-c.png', //  / 
        //       videoSource: 'https://gbs.liveqing.com:10010/play.html?serial=34020000001110000064&code=37020100001320000003&aspect=fullscreen', // 
        //       info:[
        //           // 
        //           {label: '', value: ''},
        //           {label: '', value: '2020-09-10 21:42:00'},
        //           {label: '', value: '456'},
        //           {label: '', value: 'CCVT123'},
        //           {label: '', value: 114.091058},
        //           {label: '', value: 32.148624},
        //           {label: '', value: '50'},
        //           {label: '', value: ''},

        //           // 
        //           {label: '', value: ''},
        //           {label: '', value: '2020-09-10 21:42:00'},
        //           {label: '', value: 114.091058},
        //           {label: '', value: 32.148624},
        //           {label: '', value: ''},
        //           {label: '', value: 2},
        //           {label: '', value: ''},

        //           // 
        //           {label: '', value: ''},
        //           {label: '', value: '2020-09-10 21:42:00'},
        //           {label: '', value: '456'},
        //           {label: '', value: ''},
        //           {label: 'AQI', value: '255'},
        //           {label: 'PM2.5', value: '280'},
        //           {label: 'PM10', value: '130'},
        //           {label: '', value: ''}
        //         ],
        //     }
        //   ]
        // },
        /* eslint-disable prettier/prettier*/
        { key:'site1', label: '', count: 0, ratio: 0, color: '#ff0000', disable: false, list: [] },
        { key:'site2', label: '', count: 0, ratio: 0, color: '#f27022', disable: false, list: [] },
        { key:'site3', label: '', count: 0, ratio: 0, color: '#CC45EA', disable: false, list: [] },
        { key:'site4', label: '', count: 0, ratio: 0, color: '#179eff', disable: false, list: [] }
        /* eslint-enable prettier/prettier*/
      ],
      markerDetail: null,
      markers: [], // 
      districtExplorer: null, // 
      zoom: 9, // TODO 
      map: {} // 
    }
  },
  mounted() {
    this.initData()
  },
  methods: {
    async initData() {
      this.initMap()
      this.geoDistrictExplorer()

      try {
        await this.getAlarms()
      } catch (err) {
        console.error(`[LOG]: getAlarms -> error`, err)
      }

      this.initMarkers()
    },

    /**  */
    async getAlarms() {
      // TODO  step-2 
      return await Promise.all([
        this.getChengguanAlarm(),
        this.getLandResources(),
        this.getAirPollution(),
        this.getLightPoleData()
      ])
    },

    /**  */
    async getChengguanAlarm() {
      const markers = []
      // Marker
      const res = await axios.post(`http://47.93.239.193/onenet/wx/megcity/eventsPaged`, {
        endTime: 1609401600000,
        pageNo: 1,
        pageSize: '10',
        startTime: 1577779200000
      })
      const markInfos = await Promise.all(
        // Marker
        _.map(res.data, marker => {
          const { lng, lat } = gps(115.221717, 32.0189, 40000)
          markers.push({
            lng: _.get(marker, 'location.lon', lng), // 
            lat: _.get(marker, 'location.lat', lat), // 
            name: marker.type || '', // 
            level: '', // 
            time: marker.createTimeStr || '', // 
            position: marker.address || '', // 
            cover: './cover.png', //  / 
            videoSource: ''
          })
          // 
          return axios.get(`http://47.93.239.193/onenet/wx/megcity/eventvo/${marker.caseNumber}`)
        })
      )
      // 
      _.forEach(markInfos, ({ data: info }, index) => {
        markers[index]['videoSource'] = 'http://47.93.239.193/demo.mp4' //info.causeVideoUri
        markers[index]['info'] = [
          { label: '', value: '' },
          { label: '', value: info.createTimeStr || '' },
          { label: '', value: info.address || '' },
          { label: '', value: info.deviceName || '' },
          { label: '', value: info.lon || '' },
          { label: '', value: info.lat || '' },
          { label: '', value: info.state || '' }
        ]
      })

      const alarm = this.alarms[0]
      alarm.count = markers.length
      alarm.list = markers
      // alarm.ratio = _.random(-1, 1)

      return Promise.resolve(markers)
    },

    /**  */
    async getLandResources() {
      const markers = _.map(Array(_.random(0, 10)), () => {
        const { lng, lat } = gps(115.221717, 32.0189, 40000)
        return {
          lng, // 
          lat, // 
          name: ``, // 
          level: '', // 
          time: moment().format('YYYY-MM-DD HH:mm:ss'), // 
          position: '456', // 
          cover: './cover.png', //  / 
          // videoSource: 'https://media.wxzxzj.com/the_garden_of_words_trailer_english__1080p.m3u8',
          // videoSource: 'https://bitmovin-a.akamaihd.net/content/MI201109210084_1/m3u8s-fmp4/f08e80da-bf1d-4e3d-8899-f0f6155f6efa.m3u8',
          videoSource: './VID_20200911_143902.mp4',
          info: [
            { label: '', value: '' },
            { label: '', value: moment().format('YYYY-MM-DD HH:mm:ss') },
            { label: '', value: '456' },
            { label: '', value: 'CCVT123' },
            { label: '', value: lng },
            { label: '', value: lat },
            { label: '', value: `${_.random(0, 360)}` },
            { label: '', value: '' }
          ]
        }
      })

      const alarm = this.alarms[1]
      alarm.count = markers.length
      alarm.list = markers
      // alarm.ratio = _.random(-1, 1)

      return Promise.resolve(markers)
    },

    /**  */
    async getAirPollution() {
      const markers = [
        {
          lng: '114.17315', // 
          lat: '32.18745', // 
          name: ``, // 
          level: '', // 
          time: moment().format('YYYY-MM-DD HH:mm:ss'), // 
          position: '', // 
          cover: './demo.vue-chartjs.org.png', //  / 
          info: [
            { label: '', value: '' },
            { label: '', value: moment().format('YYYY-MM-DD HH:mm:ss') },
            { label: '', value: '' },
            { label: '', value: '' },
            { label: 'AQI', value: '255' },
            { label: 'PM2.5', value: '280' },
            { label: 'PM10', value: '130' },
            { label: '', value: '' }
          ]
        },
        {
          lng: '114.16058', // 
          lat: '32.13957', // 
          name: ``, // 
          level: '', // 
          time: moment().format('YYYY-MM-DD HH:mm:ss'), // 
          position: '', // 
          cover: './demo.vue-chartjs.org.png', //  / 
          info: [
            { label: '', value: '' },
            { label: '', value: moment().format('YYYY-MM-DD HH:mm:ss') },
            { label: '', value: '' },
            { label: '', value: '' },
            { label: 'AQI', value: '177' },
            { label: 'PM2.5', value: '168' },
            { label: 'PM10', value: '190' },
            { label: '', value: '' }
          ]
        },
        {
          lng: '114.17209', // 
          lat: '32.13678', // 
          name: ``, // 
          level: '', // 
          time: moment().format('YYYY-MM-DD HH:mm:ss'), // 
          position: '', // 
          cover: './demo.vue-chartjs.org.png', //  / 
          info: [
            { label: '', value: '' },
            { label: '', value: moment().format('YYYY-MM-DD HH:mm:ss') },
            { label: '', value: '' },
            { label: '', value: '' },
            { label: 'AQI', value: '127' },
            { label: 'PM2.5', value: '125' },
            { label: 'PM10', value: '130' },
            { label: '', value: '' }
          ]
        }
      ]

      const alarm = this.alarms[2]
      alarm.count = markers.length
      alarm.list = markers
      // alarm.ratio = _.random(-1, 1)

      return Promise.resolve(markers)
    },

    /**  */
    async getLightPoleData() {
      // eslint-disable-next-line prettier/prettier
      const { data: { pole,transf } } = await axios.get('http://114.215.143.58:5502/api/Transfinfo.ashx?code=3801')

      const markers = []
      _.forEach(pole, poleItem => {
        if (poleItem.pole_state === 5) {
          const markerInfo = {
            lng: poleItem.lng_baidu, // 
            lat: poleItem.lat_baidu, // 
            name: ``, // 
            level: '', // 
            time: poleItem.fault_dt || '', // 
            point: poleItem.point || '', // 
            position: '', // 
            cover: './cover.png' //  / 
          }
          markers.push({
            ...markerInfo,
            info: [
              { label: '', value: markerInfo.name },
              { label: '', value: markerInfo.time },
              { label: '', value: markerInfo.lng || '' },
              { label: '', value: markerInfo.lat || '' },
              { label: '', value: transf.name || '' },
              { label: '', value: markerInfo.point || '' },
              { label: '', value: markerInfo.level }
            ]
          })
        }
      })

      const alarm = this.alarms[3]
      alarm.count = markers.length
      alarm.list = markers
      // alarm.ratio = _.random(-1, 1)

      return markers
      // return _.filter(pole, poleItem => poleItem.pole_state === 5)
    },

    /**  */
    initMap() {
      // 
      this.map = window.map = new AMap.Map('container', {
        // MARK  https://lbs.amap.com/dev/mapstyle/index
        mapStyle: 'amap://styles/darkblue',
        features: ['bg', 'road'],
        resizeEnable: true, // false
        expandZoomRange: false, // ,zoomstruezoomsPC2019/20
        center: [114.879901, 31.971168], // TODO 
        zooms: [4, 16], // 
        zoom: this.zoom,
        cursor: 'pointer',
        defaultCursor: 'pointer' //  defaultCursorCSScursor
        // showLabel: true, // 
      })

      this.map.on('complete', () => {
        // 
        this.map.plugin(['AMap.ToolBar'], () => {
          this.map.addControl(
            new AMap.ToolBar({
              liteStyle: true, //  false
              position: 'RT'
            })
          )
        })

        this.map.on('zoomchange', () => {
          const zoom = this.map.getZoom()
          if (this.zoom > zoom) this.geoDistrictExplorer()
          this.zoom = zoom
        })
      })
    },

    /** 
     * https://lbs.amap.com/api/amap-ui/demos/amap-ui-districtexplorer/index
     */
    geoDistrictExplorer() {
      AMapUI.loadUI(['geo/DistrictExplorer'], DistrictExplorer => {
        const districtExplorer = (this.districtExplorer = new DistrictExplorer({
          eventSupport: true, //
          map: this.map
        }))

        let currentAreaNode = null //

        //
        const renderAreaPolygons = areaNode => {
          // this.map.setBounds(areaNode.getBounds(), null, null, true) //
          districtExplorer.clearFeaturePolygons() //
          //
          districtExplorer.renderSubFeatures(areaNode, () => {
            return {
              cursor: 'default',
              bubble: true,
              strokeColor: '#00AFFF', //
              strokeOpacity: 1, //
              strokeWeight: 1, //
              fillColor: '#00AFFF', //
              fillOpacity: 0 //
            }
          })
          //
          districtExplorer.renderParentFeature(areaNode, {
            cursor: 'default',
            bubble: true,
            strokeColor: '#00AFFF', //
            strokeOpacity: 1, //
            strokeWeight: 1, //
            fillColor: '#00AFFF', //
            fillOpacity: 0 //
          })
        }

        //
        const switch2AreaNode = adcode => {
          if (currentAreaNode && '' + currentAreaNode.getAdcode() === '' + adcode) return
          districtExplorer.loadAreaNode(adcode, (error, areaNode) => {
            if (error) return
            currentAreaNode = window.currentAreaNode = areaNode
            districtExplorer.setAreaNodesForLocating([currentAreaNode]) //
            districtExplorer.setHoverFeature(null)
            renderAreaPolygons(areaNode)
          })
        }

        //featurehover
        districtExplorer.on('featureMouseout featureMouseover', (e, feature) => {
          const props = feature.properties
          const polys = districtExplorer.findFeaturePolygonsByAdcode(props.adcode)
          _.forEach(polys, poly =>
            poly.setOptions({ fillOpacity: e.type === 'featureMouseover' ? 0.2 : 0 })
          )
        })

        // //feature
        // districtExplorer.on('featureMousemove', (e, feature) => {
        //   console.log(`[LOG]: geoDistrictExplorer -> e, feature`, e, feature)
        // })

        //feature
        districtExplorer.on('featureClick', (e, feature) => {
          var props = feature.properties
          //
          // if (props.childrenNum > 0) {
          //
          switch2AreaNode(props.adcode)
          // }
        })

        //
        districtExplorer.on('outsideClick', function(e) {
          districtExplorer.locatePosition(
            e.originalEvent.lnglat,
            function(error, routeFeatures) {
              if (routeFeatures && routeFeatures.length > 1) {
                switch2AreaNode(routeFeatures[1].properties.adcode) //
              } else {
                switch2AreaNode(100000) //
              }
            },
            { levelLimit: 2 }
          )
        })

        switch2AreaNode(411500)
      })
    },

    /** Marker */
    initMarkers() {
      try {
        const _this = this

        window.onMarkerClick = (key, index) => {
          const alarm = _.find(this.alarms, alarm => alarm.key === key)
          const marker = alarm.list[index] || {}
          this.markerDetail = marker
        }

        // TODO Marker
        _.forEach(this.alarms, (alarm, index) => {
          const markers = _.map(alarm.list, (item, index) => {
            const marker = new AMap.Marker({
              position: new AMap.LngLat(item.lng, item.lat), // [116.39, 39.9]
              content: `<div class="marker ${alarm.key}">${alarm.label.substring(0, 2)}</div>`, // 
              offset: new AMap.Pixel(0, 0), // 
              anchor: 'bottom-center', // 
              extData: item
            })
            marker.on('click', function() {
              _this.markerDetail = this.getExtData()
            })
            marker.on('mouseover', function() {
              const infoWindow = new AMap.InfoWindow({
                isCustom: true, //
                content: `
  <div class="info-window" style="background-color: rgb(77,159,224,0.6);">
    <div class="info-window__head row center-x" style="background-color: ${
      alarm.color
    };color: #FFF;padding: 2px;font-size: 14px;font-weight: 900;">${alarm.label}</div>
    <h1 class="info-window__title row center-x" style="flex-wrap: nowrap;color: #fff; font-size: 16px; font-weight: 900; margin: 5px; white-space: nowrap;">
      <span class="info-window__title-txt">${item.name}</span>&nbsp;
      <span class="level" style="white-space: nowrap;font-size:10px;background-color:#f00;">${
        item.level
      }</span>
    </h1>
    <ul style="padding:5px;">
      <li class="row" style="${item.time ? '' : 'display:none'}">
        <div class="label" style="width:30px;"></div>
        <div class="value">${item.time}</div>
      </li>
      <li class="row" style="${item.position ? '' : 'display:none'}">
        <div class="label" style="width:30px;"></div>
        <div class="value">${item.position}</div>
      </li>
    </ul>
    <div class="info-window__footer" style="display:flex;justify-content:flex-end;padding:5px;">
      <a href="javascript:window.onMarkerClick('${
        alarm.key
      }',${index})" class="footer-btn" style="background-color:${
                  alarm.color
                };color:#FFF;width:100px;padding:5px;text-align:center;"></a>
    </div>
  </div>`,
                offset: new AMap.Pixel(0, -10)
              })
              infoWindow.open(_this.map, marker.getPosition())
            })
            marker.on('mouseout', function() {
              _this.map.clearInfoWindow()
            })
            return marker
          })
          this.markers.splice(index, 1, markers)
          this.map.add(markers)
        })
      } catch (err) {
        console.log(`[LOG]: initMarkers -> err`, err)
      }
    },

    /**  */
    onAlarmClick(alarm, index) {
      const enableAlarms = _.filter(this.alarms, alarm => !alarm.disable)
      const enableAlarmsSize = _.size(enableAlarms)

      if (enableAlarmsSize === this.alarms.length) {
        _.forEach(this.alarms, (alarm, i) => {
          alarm.disable = true
          this.map.remove(this.markers[i])
        })
      }

      if (enableAlarmsSize === 1 && alarm.key === enableAlarms[0]['key']) {
        _.forEach(this.alarms, (alarm, i) => {
          alarm.disable = false
          this.map.add(this.markers[i])
        })
        this.map.remove(this.markers[index])
      }

      this.map.add(this.markers[index])
      alarm.disable = false

      // polylinemarker1
      // this.map.setFitView(this.markers[index]);
    }
  }
}
</script>

